import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import '../pages/Menu/menu.css';
import IconButton from '@material-ui/core/IconButton';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';

function Product(props) {
  const { id, img, name, price } = props;

  const [isBtn, setIsBtn] = useState('menu');
  const location = useLocation();
  useEffect(() => {
    const location = window.location.pathname;
    console.log(location);
    const changeBtn = () => {
      if (location === '/menu') {
        setIsBtn('menu');
      } else if (location === '/order') {
        setIsBtn('order');
      } else if (location === '/follow') {
        setIsBtn('follow');
      }
    };
    changeBtn();
  }, [location]);

  const addToCart = () => {
    const cart = JSON.parse(localStorage.getItem('cart'));
    console.log(cart);
    cart.push({ id, name, img, price });
    console.log('add ' + name + id);
    console.log(cart);
    localStorage.setItem('cart', JSON.stringify(cart));
  };

  const removeFromCart = () => {
    let cart = JSON.parse(localStorage.getItem('cart'));
    cart = cart.filter((product) => product.id !== id);
    console.log();
    console.log(cart);
    localStorage.setItem('cart', JSON.stringify(cart));
    window.location.reload();
  };

  const productBtn = () => {
    let btn;
    console.log(isBtn, isBtn === 'menu');
    if (isBtn === 'menu') {
      btn = (
        <IconButton className='addBtn' aria-label='delete' onClick={addToCart}>
          <AddCircleIcon fontSize='large' style={{ color: '#fc885c' }} />
        </IconButton>
      );
    } else if (isBtn === 'order') {
      btn = (
        <IconButton
          className='addBtn'
          aria-label='delete'
          onClick={removeFromCart}
        >
          <HighlightOffIcon fontSize='large' style={{ color: '#3addda' }} />
        </IconButton>
      );
    } else if (isBtn === 'follow') {
      btn = <div></div>;
    }
    return btn;
  };

  return (
    <div className='productContainer'>
      <img src={img} alt='Food' />
      <h3>{name}</h3>
      <h6>${price}</h6>
      {productBtn()}
    </div>
  );
}

export default Product;
