import React from 'react';
import { OrderItems } from '../Order/Order';
import { useHistory } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { Steps } from 'antd';
import './follow.css';

function Follow() {
  const { Step } = Steps;

  return (
    <div className='follow'>
      <section>
        <h3 className='title'>Estado</h3>
        <Steps className='steps' progressDot direction='vertical' current={2}>
          <Step title='Confirmado' />
          <Step title='En preparación' />
          <Step title='En camino' />
          <Step title='Entregado' />
        </Steps>
      </section>

      <OrderItems className='order' />
      <Btns />
    </div>
  );
}

const Btns = () => {
  const history = useHistory();
  const toMenu = () => {
    history.push('/menu');
  };
  const toConfirmed = () => {
    history.push('/confirmed');
  };
  return (
    <div className='followBtn'>
      <Button className='submitBtn' variant='contained' onClick={toConfirmed}>
        Cancelar orden
      </Button>
      <Button className='linkBtn' variant='outlined' onClick={toMenu}>
        Volver al menu
      </Button>
    </div>
  );
};

export default Follow;
