import React, { useState } from 'react';
import { Button } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import './confirmed.css';

function Confirmed() {
  const history = useHistory();
  const toFollow = () => {
    history.push('/follow');
  };
  const toOrder = () => {
    history.push('/order');
  };

  const [name, setName] = useState('Pepito');

  return (
    <div className='confirmed'>
      <img src='./images/confirmed.png' alt='Confirmed' />
      <h1 className='title'>¡Recibimos tu pedido!</h1>
      <p className='msg'>
        {name}, gracias por pedir a Delilah. Puedes seguir tu pedido para saber
        dónde está.
      </p>
      <Button className='submitBtn' variant='contained' onClick={toFollow}>
        Seguir pedido
      </Button>
      <Button className='linkBtn' variant='outlined' onClick={toOrder}>
        Volver a la orden
      </Button>
    </div>
  );
}

export default Confirmed;
